:root {
    font-size: calc(15px + 6*(100vw - 320px)/680);
}

#App,
#root,
body,
html {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    color: #fff!important;
    font-family: system-ui, BlinkMacSystemFont, -apple-system, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, sans-serif;
    text-align: center;
}

main{
    display:block;
}

h1 {
    text-transform: uppercase;
    text-align: center;
    font-size: 2.8em;
}

h1,
h2,
h3 {
    font-family: 'PT Serif', serif;
    letter-spacing: 0.05em;
    margin-bottom: 1rem;
}

.normal-case {
    text-transform: none;
}

.text-2xl {
    font-size: 1.5rem;
}

html {
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    box-sizing: border-box;
}

.hidden {
    display: none;
}

.fullBG {
    position: absolute;
    padding: 1rem;
    width: 100%;
    min-height: 100%;
    height: auto;
    background-blend-mode: multiply;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: 100% auto;
    background-image: linear-gradient(0deg, #145185, #1868AC);
}

.nh3-univers {
    List-style: none;
    margin: 0;
    padding: 0;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
}

.nh3-univers h2 {
    text-align:center;
    border:2px solid #fff;
    padding: .4em .3em;
    font-size:1.25rem;
}

.nh3-univers h2 a {
    text-decoration: none;
}

.slogan {
    font-style: italic;
}

.btn__primary {
    border: 2px solid #fff;
    padding: .5em 1.5em;
    text-transform: uppercase;
}

a.btn__primary,
.btn {
    font-size: 100%;
    line-height: 1.15;
    margin: 0;
    color: #fff;
    text-decoration: none;
}

a {
    color: #fff;
}

*,
 ::after,
 ::before {
    box-sizing: inherit;
}

.flex-col {
    -webkit-flex-direction: column;
            flex-direction: column;
}

.flex {
    display: -webkit-flex;
    display: flex;
}

.relative {
    position: relative;
}

.mt-auto {
    margin-top: auto;
}

.justify-center {
    -webkit-justify-content: center;
            justify-content: center;
}

.justify-between {
    -webkit-justify-content:space-between;
            justify-content:space-between;
}

#target img{
  width:100%;
  height:auto;
  max-height: 100%;
}

#mosaic {
  z-index: 10;
}

#intro {
  text-align: center;
  padding: 10px 0 20px 0;
  background: rgb(73, 127, 245);
  margin: 0 0 1px 0;
}

.hidden {
  display: none;
}
.camera{
    z-index:200;
    position: absolute;
    height:100%;
    width:100%;
}
.description{
    width:100%;
    height:auto;
    position:fixed;
    bottom:0px;
    z-index:30;
    padding: 20px;
    background-blend-mode: multiply;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: 100% auto;
    background-image: linear-gradient(0deg, #145185, #1868AC);    
    text-align: left;
    box-sizing: border-box;
}

.description h2{
    margin:20px 0 0 0;
}
.description h4{
    margin:0;
}

.description p{
    font-size:12px;
}

.description a {
    font-style: italic;
    font-size:10px;
    color:white;
    float: right;
}
.btn--close {
    background-color: #fff;
    position: absolute;
    right: 0;
    width: 32px;
    height: 32px;
}

.btn--close::after, .btn--close::before {
    position: absolute;
    top: 4px;
    left: 15px;
    content: " ";
    height: 24px;
    width: 2px;
    background-color: #145185;
}

.btn--close::after {
    -webkit-transform: rotate(-45deg);
    transform: rotate(-45deg);
}

.btn--close::before {
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
}

#home footer {
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-items: center;
}

#home footer :first-child {
    justify-self: start;
}

#home footer>* {
    padding: 0.5rem;
}
